import React, {Component} from "react";
import { withRouter, Redirect } from 'react-router-dom'
import { logPnsIn } from '../../actions/pns'
import { connect } from 'react-redux'
import '../../assets/css/login.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const authenticationChecking = (props)=> {
  if (props.isAuthenticated) {
    return <Redirect from='/login' to='/' />
  }
  return null
}

const handleOnSubmit = (e, props) => {
  e.preventDefault()
  props.logPnsIn({
    authorization: e.target.querySelector('input#authorization').value,
    password: e.target.querySelector('input#password').value
  })
}

const handleInformation = (props)=> {
  if (props.hasError.form.status) {
    return (
      <div style={{'fontSize': '12px'}} className="alert alert-danger">
        <FontAwesomeIcon className="fai-margin fai-lg" icon="user-times" /> {props.hasError.form.message}
      </div>
    )
  } else if (props.isSuccess.status) {
    return (
      <div style={{'fontSize': '12px'}} className="alert alert-success">
        <FontAwesomeIcon className="fai-margin fai-lg" icon="user-check" /> {props.isSuccess.message}
      </div>
    )
  } else {
    return null
  }
}

const handleOnFormLoading = (props)=> {
  let _span, _isdisabled;
  if (props.isLoading.form.status) {
    _span = props.isLoading.form.message
    _isdisabled = true
  } else {
    _span = 'Login'
    _isdisabled = false
  }
  return <input className={(_isdisabled ? "authenticating" : "signin") } type="submit" disabled={ _isdisabled} value={_span} />
}

class Login extends Component {
  render() {
    return (
      <div className="body">
      { authenticationChecking(this.props)}
        <div className="container">
          <div className="content">
            <div className="row">
              <div className="col-md-6 kumham">
                <img src={'img/logo_fix.png'} className="logosso" alt="logo" />
              </div>
              <div className="col-md-6 form">
                <form onSubmit={(e)=> handleOnSubmit(e, this.props)}>
                  <div className="konten">
                    <div className="text-center" style={{ minHeight: 60 + 'px' }}>
                      { handleInformation(this.props) }
                    </div>
                    <h3 className="login">Login</h3>
                    <label className="field field_animated field_a2 page__field">
                      <input id="authorization" className="field__input" placeholder="Masukkan NIP / Username" name="authorization" />
                      <span className="field__label-wrap">
                          <span className="field__label">Authorization</span>
                      </span>
                    </label>
                    <label className="field field_animated field_a2 page__field">
                        <input id="password" className="field__input" placeholder="Masukkan Password" name="password" type="password" />
                        <span className="field__label-wrap">
                            <span className="field__label">Password</span>
                        </span>
                    </label><br />
                    <div className="col-xs-12">
                      { handleOnFormLoading(this.props) }
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
      </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    isLoading: state.apiProcess.isLoading,
    hasError: state.apiProcess.hasError,
    isSuccess: state.apiProcess.isSuccess,
    isAuthenticated: state.isAuthenticated
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logPnsIn: (data) => dispatch(logPnsIn(data)),
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));



