import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getAllowedApplications } from '../../actions/pns';
import Application from './Application';
import '../../assets/css/dashboard.css'

const renderingApplications = (props)=> {
  let _applications = [];
  props.applications.forEach((item, index)=> {
    _applications.push(<Application data={item} />)
  })
  return _applications;
}

class Dashboard extends Component {
  componentDidMount() {
      this.props.getAllowedApplications()
  }

  render() {
    return (
      <div className="row dashboard">
        { renderingApplications(this.props) }
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    applications: state.allowedApplications
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
    getAllowedApplications: ()=> dispatch(getAllowedApplications())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Dashboard));



