import React, {Component} from 'react';
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import '../../assets/css/header.css'
import Cookie from 'universal-cookie'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { logPnsOut } from '../../actions/pns'

const cookie = new Cookie();

const handleLogout = (props)=> {
  props.logout();
}

class Header extends Component {

  constructor(props) {
      super(props);
      this.state = {
        isToggleOn: false
      };
    }
    showDropdown(e) {
      e.preventDefault();
      this.setState(prevState => ({
        isToggleOn: !prevState.isToggleOn
      }));
    }

  render() {
    const classDropdownMenu = 'dropdown-menu' + (this.state.isToggleOn ? ' show' : '');
    return (
      <nav className="navbar navbar-expand navbar-dark bg-dark fixed-top">
        <div className="container">
          <a className="navbar-brand" href="http://www.kemenkumham.go.id" target="_blank">
            <img className="navbrandimage" src={'img/kemenkumham.png'} width="75px" />
            <div className="containertitle">
              <span className="navtitle navtitlesso">Single Sign On (SSO)</span>
              <span className="navtitle navtitlekemenkumham">Kementerian Hukum dan Hak Asasi Manusia RI</span>
            </div>
          </a>
          <div className="navbar-nav ml-md-auto">
            <img className="userimg rounded-circle d-block mx-auto" src={'img/userimg.png'} width="60px" height="60px" />
            <div className="userinfo">
              <span className="username">{ cookie.get('kemenkumhamssoengine_pns').name ? cookie.get('kemenkumhamssoengine_pns').name : 'Kemenkumham' }</span> <br />
              <span className="usernip">{ cookie.get('kemenkumhamssoengine_pns').nip ? cookie.get('kemenkumhamssoengine_pns').nip : ''}</span>
            </div>
            <div className="usermenu dropdown">
              <button className="btn btn-dark" onClick={(e)=> this.showDropdown(e)} id="dropdownMenuButton">
                <FontAwesomeIcon icon="ellipsis-v" />
              </button>
              <div className={classDropdownMenu} aria-labelledby="dropdownMenuButton">
                <button className="dropdown-item" href="#"> <FontAwesomeIcon className="fai-margin fai-lg" icon="users-cog" /> Profile </button>
                <button className="dropdown-item" onClick={(e)=> {this.showDropdown(e), handleLogout(this.props)}}><FontAwesomeIcon className="fai-margin fai-lg" icon="sign-out-alt" /> Log Out</button>
              </div>
            </div>
          </div>
        </div>
      </nav>
    )
  }
}


const mapStateToProps = (state) => {
  return {
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logout:()=> dispatch(logPnsOut())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header))



