import React, {Component} from 'react';
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

const integratedApplications = [
  'e-planning', 'simasham'
];

const handleClickApp = (e, url, name)=> {
  e.preventDefault();
  let _isIntegrated = false;
  integratedApplications.forEach((application, index)=> {
    if (application === name.toLowerCase())  {
      _isIntegrated = true;
    }
  })
  if (_isIntegrated) {
    window.open(url, "_blank");
  } else {
    alert(name + ' belum terintegrasi.');
  }
}

const displayaccounts = (accounts, url)=> {
  let _accountsview = [];
  accounts.forEach((account, index)=> {
    _accountsview.push(
      <button className="dropdown-item" onClick={()=> window.open(url + 'sso/' + account.username)}> { account.name }</button>
    )
  });
  return _accountsview;
}

const handleListApplication = (data, classuseraccountsdropdownmenu, showDropdown)=> {
  if (data.accounts != null) {
    if (data.accounts.length > 1) {
      return (
        <div className="dropdown">
          <div className="useraccounts dropdown">
            <div id="dropdownobject">
              <img className="applicationimage rounded-circle d-block mx-auto" onClick={(e)=> showDropdown(e)} src={'img/' + data.image } width="80px" height="80px" />
              <h4> { data.name }</h4>
              <p>{ data.description }</p>
            </div>
            <div className={ classuseraccountsdropdownmenu } aria-labelledby="dropdownobject">
              {displayaccounts(data.accounts, data.url)}
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <a className="applicationitem" onClick={(e)=> handleClickApp(e, (data.url + 'sso/' + data.accounts[0].username), data.name)}>
            <img className="applicationimage rounded-circle d-block mx-auto" src={'img/' + data.image } width="80px" height="80px" />
          </a>
          <h4> { data.name }</h4>
          <p>{ data.description }</p>
        </div>
      );
    }
  } else {
    return (
      <div>
        <a className="applicationitem" onClick={(e)=> handleClickApp(e, data.url, data.name)}>
          <img className="applicationimage rounded-circle d-block mx-auto" src={'img/' + data.image } width="80px" height="80px" />
        </a>
        <h4> { data.name }</h4>
        <p>{ data.description }</p>
      </div>
    );
  }
}

class Application extends Component {

  constructor(props) {
      super(props);
      this.state = {
        isToggleOn: false
      };
    }
    showDropdown(e, me) {
      e.preventDefault();
      me.setState(prevState => ({
        isToggleOn: !prevState.isToggleOn
      }));
    }

  render() {
    const classuseraccountsdropdownmenu = 'dropdown-menu' + (this.state.isToggleOn ? ' show' : '');
    return (
      <div className="col-lg-3 col-sm-6 text-center mb-3">
       { handleListApplication(this.props.data, classuseraccountsdropdownmenu, (e)=> this.showDropdown(e, this))}
      </div>
    )
  }
}

export default withRouter(connect(null, null)(Application))



